.discription{
    grid-column: 1/-1;
    text-align: center;
    &__text{
        margin-top: 1.5rem;
        text-align: center;
        word-spacing: .25rem;
        font-size: 1.1rem;
    }
}
.comp{
    &-fig__img{
        width: 100%;
        display: block;
    }
    &-dis{
        padding-left: 4rem;
        display: grid;
        grid-template-rows: repeat(3,1fr);
        padding-top: 2rem;
        @media only screen and (max-width:$bp-large){
            text-align: center;
            align-items: center;
            justify-content: center;
            row-gap: 2rem;
        }
        &__p{
            margin-top: 2rem;
            font-size: 1.3rem;
            font-weight: 400;
            
            word-spacing: .3rem;
            letter-spacing: .1rem;
            
        }
    }
}
.access{
    grid-column: 1/-1;
    text-align: center;
}
.mob-fig{
    display: flex;
    align-items: center;
    justify-content: center;
    grid-column: 1/-1;
}
.mob__img{
    width: 100%;
}
.supercharge{
    grid-column: 1/-1;
    text-align:center;
}
.properties{
    grid-column: 1/-1;
    text-align: center;
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(300px,1fr));
    column-gap: 4rem;
    align-content: center;
    justify-content: center;
    @media only screen and (max-width:$bp-largest){
        text-align: center;
        align-items: center;
        justify-content: center;
        row-gap: 4rem;
        &__sec--3{
            grid-column: 1/-1;
        }
    }
    &__logo{
        margin-bottom: 3rem;
        
    }
    &__logo--1{
        margin-bottom:2rem ;
    }
    
}
.seenon{
    margin-top: 7rem;
    grid-column: 1/-1;
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(200px,1fr));
    text-align: center;
    align-content: center;align-items: center;
    &__fig{
        display: block;
    }

    @media only screen and (max-width:$bp-largest){
       row-gap: 2rem;
       &__fig--4{
        grid-column: 1/2;
        }
        &__fig--5{
            grid-column: 3/4;
        }
    }
    @media only screen and (max-width:$bp-small){
        grid-template-columns: repeat(auto-fit,minmax(200px,1fr));
        &__fig--4{
            grid-column: auto;
            }
        &__fig--5{
                grid-column:1/-1;
        }
    }
}
.footer{
    
    grid-column: 1/-1;
    display:grid;
    grid-template-columns: 1fr 2fr 1fr;
    text-align: center;
    align-items: center;
    @media only screen and (max-width:$bp-large){
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr 1fr;
        row-gap:2rem;
    }
    
    &__logo{
        height: 25%;
    }
    .logo__img-1{
        height: 100%;
        
    }
    &__content{
        display: grid;
        grid-template-columns: repeat(auto-fit,minmax(150px,1fr));
        column-gap: 4rem;
        row-gap: 2rem;
        @media only screen and (max-width:$bp-large){
            grid-template-columns: 1fr;
            row-gap: 1rem;
        }
        
    }
    &__content--text{
        text-decoration: none;
        color: #000;
        font-size:1.5rem;
        cursor: pointer;
        padding: 1rem 0;
        transition: all 0.2s;
        &:hover{
            background-color: var(--Grayish-Blue);
            color: #fff;
        }
    }
    &__media{
        
        &--icon{
            margin-left: 1rem;
        }
    }
}
