.btn{
    padding: 1.5rem 4rem;
    font-size: 1.5rem;
    border: none;
    border-radius: 10rem;
    color: #fff;
    margin-bottom: 1rem;
    &--green{
        background-color: var(--Strong-Cyan);
        transition: all 0.2s;
        &:hover{
            background-color: hsl(190, 60%, 50%);
        }
    }
    &--blue{
        background-color: var(--Light-Blue);
        transition: all 0.2s;
        &:hover{
            background-color:hsl(250, 100%, 69%);
        }
    }
}