.header{
    text-align: center;
    grid-column: 1/-1;
    display: grid;
    grid-template-rows: repeat(3,min-content);
    row-gap: 4rem;
    
    &__text{
        text-align: center;
    }
    &__p{
        margin-top: 1.5rem;
        text-align: center;
        word-spacing: .25rem;
        font-size: 1.1rem;
    }
}
.logo{
    padding-top:5rem ;
    padding-bottom: 2rem;
    background-image: url(../images/bg-header-desktop.png);
    background-size: cover;
    background-position: center;
    &__img{
        height: 100%;
    }
}